export default {
  layout: "Emp-content",
  mounted() {
    this.getDesignation();

  },
  data() {
    return {
      designations: '',
      designation: {
        type: "",
        name: '',
      },
      errors: {},
    };
  },
  methods: {
    getDesignation() {
      this.$axios
        .$get("/designation/show")
        .then((res) => {
          this.designations = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    designationEdit(id) {
      $("#designationUpdate").modal("show");
      this.$axios
        .$get("/designation/edit/" + id)
        .then((res) => {
          this.designation = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    designationUpdate() {
      this.$axios
        .$post("/designation/update/" + this.designation.id, this.designation)
        .then((res) => {
          this.getDesignation();
          $("#designationUpdate").modal("hide");
          this.$toaster.success(res.message);
          this.errors = "";


        })
        .catch((err) => {
          console.log(err);
          this.errors = err.response.data.errors;
        });
    },
    designationDelete(id) {
      if (confirm("Are you sure to delete this designation?")) {
        this.$axios
          .$get("/designation/delete/" + id)
          .then((res) => {
            this.getDesignation();
            this.$toaster.error(res.message);
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    designationStatus(id) {
      this.$axios
        .$get("/designation/status/" + id)
        .then((res) => {
          console.log(res);
          this.getDesignation();
          this.$toaster.success(res.message);
        })
        .catch((err) => {
          console.log(err);
        });
    },





  },
};
